.App-Subjects {
    .section {
        margin-left: 35px;
        margin-top: 35px;
        h1 {
            font-size: 22px;
            margin-bottom: 15px;
        }
        .input {
            display: flex;
            justify-content: space-between;
            width: 250px;
            .add {
                border: 0;
                background: #13db56;
                color: white;
                padding: 10px;
                border-radius: 5px;
                cursor: pointer;
                transition: .3s cubic-bezier(0.39, 0.575, 0.565, 1);
                &:hover {
                    background: #09c849;
                }
            }
        }
    }
}