@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}body{font-family:"Poppins",sans-serif}*{font-family:"Poppins",sans-serif}
.App-Login{display:flex;align-items:center;justify-content:center;margin-top:50px}.App-Login .everything{display:flex;align-items:center;flex-direction:column;width:400px;height:500px;background:#242424}.App-Login .everything h1{color:#fff;margin-top:25px;font-size:24px}.App-Login .everything .inputs{display:flex;flex-direction:column;margin-top:50px}.App-Login .everything .inputs .input-box{margin-top:10px;padding:11px;font-size:14px;border-radius:5px;border:0}.App-Login .everything .btn--login{background:transparent;padding:15px;border:5px solid #fff;border-radius:5px;margin-top:30px;color:#fff;transition:.3s cubic-bezier(0.86, 0, 0.07, 1)}.App-Login .everything .btn--login:hover{background:#fff;color:#000;cursor:pointer}
.App-Home .text{display:flex;align-items:center;flex-direction:column;justify-content:center;margin-top:50px}.App-Home .text h1{font-size:26px;font-weight:bold}.App-Home .text p{font-size:20px;margin-top:5px}
.App-Header .header{width:100%;display:flex;align-items:center;justify-content:space-between;margin:0;height:100px;background:#8744f3}.App-Header .header h2{color:#fff;font-size:28px;cursor:pointer;margin-left:100px}.App-Header .header .buttons{display:flex;flex-direction:row;margin-right:100px}.App-Header .header .buttons a{text-decoration:none}.App-Header .header .buttons a p{color:#fff;transition:.4s ease-in-out;display:flex;align-items:center;justify-content:center;height:100px;width:125px;cursor:pointer}.App-Header .header .buttons a p:hover{background:#6b2ccf}
.App-Classes .input{display:flex;justify-content:space-between;width:275px;margin-left:35px;margin-top:35px}.App-Classes .input .add{border:0;background:#13db56;color:#fff;padding:10px;border-radius:5px;cursor:pointer;transition:.3s cubic-bezier(0.39, 0.575, 0.565, 1)}.App-Classes .input .add:hover{background:#09c849}
.App-Plans h1{font-size:26px;display:flex;align-items:center;justify-content:center;padding:12px 0}.App-Plans .table-center{display:flex;align-items:center;justify-content:center}.App-Plans .table-center .table{display:flex;flex-direction:row;width:90%;height:450px;background:#e6e6e6}.App-Plans .table-center .table .column{border:1px solid #000;width:20%;display:flex;align-items:center;flex-direction:column}.App-Plans .table-center .table .column p{margin-top:10px}.App-Plans .table-center .table .column .sub{width:90%;display:flex;flex-direction:column}.App-Plans .table-center .table .column .sub .selection{display:flex;flex-direction:column;margin-top:13px;font-size:14px}.App-Plans .table-center .table .column .sub .selection span{margin-bottom:2px}
.App-Subjects .section{margin-left:35px;margin-top:35px}.App-Subjects .section h1{font-size:22px;margin-bottom:15px}.App-Subjects .section .input{display:flex;justify-content:space-between;width:250px}.App-Subjects .section .input .add{border:0;background:#13db56;color:#fff;padding:10px;border-radius:5px;cursor:pointer;transition:.3s cubic-bezier(0.39, 0.575, 0.565, 1)}.App-Subjects .section .input .add:hover{background:#09c849}
