.App-Login {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
    .everything {
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 400px;
        height: 500px;
        background: #242424;
        h1 {
            color: white;
            margin-top: 25px;
            font-size: 24px;
        }
        .inputs {
            display: flex;
            flex-direction: column;
            margin-top: 50px;
            .input-box {
                margin-top: 10px;
                padding: 11px;
                font-size: 14px;
                border-radius: 5px;
                border: 0;
            }
        }
        .btn {
            &--login {
                background: transparent;
                padding: 15px;
                border: 5px solid white;
                border-radius: 5px;
                margin-top: 30px;
                color: white;
                transition: .3s cubic-bezier(0.86, 0, 0.07, 1);
                &:hover {
                    background: white;
                    color: black;
                    cursor: pointer;
                }
            }
        }
    }
}