.App-Plans {
    h1 {
        font-size: 26px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 12px 0;
    }
    .table-center {
        display: flex;
        align-items: center;
        justify-content: center;
        .table {
            display: flex;
            flex-direction: row;
            width: 90%;
            height: 450px;
            background: #e6e6e6;
            .column {
                border: 1px solid black;
                width: 20%;
                display: flex;
                align-items: center;
                flex-direction: column;
                p {
                    margin-top: 10px;
                }
                .sub {
                    width: 90%;
                    display: flex;
                    flex-direction: column;
                    .selection {
                        display: flex;
                        flex-direction: column;
                        margin-top: 13px;
                        font-size: 14px;
                        span {
                            margin-bottom: 2px;
                        }
                    }
                }
            }
        }
    }
}