.App-Header {
    .header {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0;
        height: 100px;
        background: #8744f3;
        h2 {
            color: white;
            font-size: 28px;
            cursor: pointer;
            margin-left: 100px;
        }
        .buttons {
            display: flex;
            flex-direction: row;
            margin-right: 100px;
            a {
                text-decoration: none;
                p {
                    color: white;
                    transition: .4s ease-in-out;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    height: 100px;
                    width: 125px;
                    cursor: pointer;
                    &:hover {
                        background: #6b2ccf;
                    }
                }
            }
        }
    }
}